// hooks/useAlert.ts
'use client';
import { useState, useCallback } from 'react';

export type AlertType = 'success' | 'error';

interface AlertState {
  isVisible: boolean;
  type: AlertType;
  message: string;
}

export function useAlert() {
  const [alert, setAlert] = useState<AlertState>({
    isVisible: false,
    type: 'success',
    message: ''
  });

  const showAlert = useCallback((type: AlertType, message: string) => {
    setAlert({
      isVisible: true,
      type,
      message
    });
  }, []);

  const hideAlert = useCallback(() => {
    setAlert(prev => ({ ...prev, isVisible: false }));
  }, []);

  const handleLinkClick = useCallback((url: string | undefined, type: 'demo' | 'github') => {
    if (!url || url === '#' || url === '') {
      const message = type === 'demo' 
        ? 'Demo não disponível no momento. Entre em contato para mais informações!'
        : 'Código fonte não disponível publicamente.';
      
      showAlert('error', message);
      return false;
    }
    
    // Se tem URL válida, abre em nova aba
    window.open(url, '_blank');
    return true;
  }, [showAlert]);

  return {
    alert,
    showAlert,
    hideAlert,
    handleLinkClick
  };
}