// hooks/usePortfolio.ts
'use client';
import { useState } from 'react';
import { useLanguage } from '@/contexts/LanguageContext';
import { useAlert } from './useAlert';

export interface Project {
  id: number;
  title: string;
  description: string;
  category: 'web' | 'api' | 'automation';
  image: string;
  technologies: string[];
  link?: string;
  github?: string;
  demo?: string;
  details?: {
    longDescription: string;
    features: string[];
  };
}

export function usePortfolio() {
  const { language } = useLanguage();
  const { handleLinkClick } = useAlert();
  
  const [activeFilter, setActiveFilter] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  const projectsPerPage = 4;

  const filters = [
    { key: 'all', label: language === 'pt' ? 'Todos os Projetos' : 'All Projects' },
    { key: 'web', label: language === 'pt' ? 'Web/Frontend' : 'Web/Frontend' },
    { key: 'api', label: 'API' },
    { key: 'automation', label: language === 'pt' ? 'Automação' : 'Automation' }
  ];

  const projects: Project[] = [
    {
      id: 1,
      title: language === 'pt' ? 'Plataforma E-commerce' : 'E-commerce Platform',
      description: language === 'pt' 
        ? 'Solução completa de e-commerce com design responsivo e performance otimizada'
        : 'Complete e-commerce solution with responsive design and optimized performance',
      category: 'web',
      image: '/images/img005.png',
      technologies: ['React', 'Next.js', 'TypeScript', 'Tailwind CSS'],
      demo: '', // Link vazio para teste
      github: '', // Link vazio para teste
      details: {
        longDescription: language === 'pt' 
          ? 'Plataforma de e-commerce completa com catálogo de produtos, carrinho de compras, sistema de pagamentos integrado e painel administrativo. Desenvolvida com foco em performance e experiência do usuário.'
          : 'Complete e-commerce platform with product catalog, shopping cart, integrated payment system and admin panel. Developed with focus on performance and user experience.',
        features: [
          language === 'pt' ? 'Catálogo de produtos com filtros' : 'Product catalog with filters',
          language === 'pt' ? 'Carrinho de compras' : 'Shopping cart',
          language === 'pt' ? 'Integração com gateways de pagamento' : 'Payment gateway integration',
          language === 'pt' ? 'Painel administrativo' : 'Admin panel',
          language === 'pt' ? 'Design responsivo' : 'Responsive design',
          language === 'pt' ? 'Otimização SEO' : 'SEO optimization'
        ]
      }
    },
    {
      id: 2,
      title: language === 'pt' ? 'API de Pagamentos' : 'Payments API',
      description: language === 'pt'
        ? 'API segura para processamento de pagamentos com múltiplos métodos'
        : 'Secure API for payment processing with multiple methods',
      category: 'api',
      image: '/images/img004.png',
      technologies: ['.NET Core', 'PostgreSQL', 'Docker', 'JWT'],
      github: '#', // Link # para teste
      details: {
        longDescription: language === 'pt'
          ? 'API RESTful segura para processamento de pagamentos com suporte a múltiplos métodos. Inclui sistema de webhooks para notificações e dashboard de transações.'
          : 'Secure RESTful API for payment processing with support for multiple methods. Includes webhook system for notifications and transaction dashboard.',
        features: [
          language === 'pt' ? 'Processamento de pagamentos' : 'Payment processing',
          language === 'pt' ? 'Webhooks para notificações' : 'Webhooks for notifications',
          language === 'pt' ? 'Dashboard de transações' : 'Transaction dashboard',
          language === 'pt' ? 'Multiplos métodos de pagamento' : 'Multiple payment methods',
          language === 'pt' ? 'Autenticação JWT' : 'JWT authentication',
          language === 'pt' ? 'Documentação Swagger' : 'Swagger documentation'
        ]
      }
    },
    {
      id: 3,
      title: language === 'pt' ? 'Sistema de Automação' : 'Automation System',
      description: language === 'pt'
        ? 'Sistema inteligente para automação de processos e tarefas repetitivas'
        : 'Intelligent system for process automation and repetitive tasks',
      category: 'automation',
      image: '/images/img006.png',
      technologies: ['.NET Core', 'Hangfire', 'BackgroundService', 'Web Scraping'],
      github: '', // Link vazio para teste
      details: {
        longDescription: language === 'pt'
          ? 'Sistema de automação completo para processamento em background e execução de tarefas agendadas. Ideal para workflows empresariais e processamento de dados.'
          : 'Complete automation system for background processing and scheduled tasks execution. Ideal for business workflows and data processing.',
        features: [
          language === 'pt' ? 'Tarefas agendadas' : 'Scheduled tasks',
          language === 'pt' ? 'Processamento em background' : 'Background processing',
          language === 'pt' ? 'Web scraping' : 'Web scraping',
          language === 'pt' ? 'Monitoramento em tempo real' : 'Real-time monitoring',
          language === 'pt' ? 'Relatórios automáticos' : 'Automatic reports',
          language === 'pt' ? 'Escalabilidade horizontal' : 'Horizontal scalability'
        ]
      }
    },
    {
      id: 4,
      title: language === 'pt' ? 'Dashboard Analytics' : 'Analytics Dashboard',
      description: language === 'pt'
        ? 'Painel interativo com métricas em tempo real e visualizações avançadas'
        : 'Interactive dashboard with real-time metrics and advanced visualizations',
      category: 'web',
      image: '/images/img003.png',
      technologies: ['React', 'TypeScript', 'Chart.js', 'REST API'],
      demo: '#', // Link # para teste
      github: 'https://github.com/seuuser/analytics-dashboard', // Link válido
      details: {
        longDescription: language === 'pt'
          ? 'Dashboard de analytics em tempo real com visualizações interativas e relatórios personalizáveis. Suporta múltiplas fontes de dados e alertas inteligentes.'
          : 'Real-time analytics dashboard with interactive visualizations and customizable reports. Supports multiple data sources and smart alerts.',
        features: [
          language === 'pt' ? 'Visualizações em tempo real' : 'Real-time visualizations',
          language === 'pt' ? 'Relatórios personalizáveis' : 'Customizable reports',
          language === 'pt' ? 'Alertas inteligentes' : 'Smart alerts',
          language === 'pt' ? 'Multiplas fontes de dados' : 'Multiple data sources',
          language === 'pt' ? 'Exportação de dados' : 'Data export',
          language === 'pt' ? 'Dashboard responsivo' : 'Responsive dashboard'
        ]
      }
    },
    {
      id: 5,
      title: language === 'pt' ? 'Sistema de Autenticação' : 'Authentication System',
      description: language === 'pt'
        ? 'Sistema seguro de autenticação com múltiplos fatores e gestão de permissões'
        : 'Secure authentication system with multi-factor and permission management',
      category: 'api',
      image: '/images/img007.png',
      technologies: ['ASP.NET Core', 'JWT', 'PostgreSQL', 'Docker'],
      github: '', // Link vazio para teste
      details: {
        longDescription: language === 'pt'
          ? 'Sistema completo de autenticação e autorização com suporte a MFA, OAuth2, gestão de roles e permissões granulares. Arquitetura segura e escalável.'
          : 'Complete authentication and authorization system with MFA support, OAuth2, roles management and granular permissions. Secure and scalable architecture.',
        features: [
          language === 'pt' ? 'Autenticação multi-fator (MFA)' : 'Multi-factor authentication (MFA)',
          language === 'pt' ? 'Gestão de roles e permissões' : 'Roles and permissions management',
          language === 'pt' ? 'Integração OAuth2' : 'OAuth2 integration',
          language === 'pt' ? 'Logs de segurança' : 'Security logs',
          language === 'pt' ? 'Refresh tokens' : 'Refresh tokens',
          language === 'pt' ? 'Rate limiting' : 'Rate limiting'
        ]
      }
    },
    {
      id: 6,
      title: language === 'pt' ? 'Bot de Automação' : 'Automation Bot',
      description: language === 'pt'
        ? 'Bot inteligente para automação de tarefas repetitivas e integração entre sistemas'
        : 'Intelligent bot for task automation and system integration',
      category: 'automation',
      image: '/images/img008.png',
      technologies: ['.NET Core', 'Hangfire', 'Web Scraping', 'REST API'],
      github: 'https://github.com/seuuser/automation-bot', // Link válido
      details: {
        longDescription: language === 'pt'
          ? 'Bot de automação que utiliza técnicas de web scraping e integração de APIs para automatizar processos empresariais. Inclui dashboard de monitoramento e relatórios.'
          : 'Automation bot that uses web scraping techniques and API integration to automate business processes. Includes monitoring dashboard and reports.',
        features: [
          language === 'pt' ? 'Web scraping inteligente' : 'Smart web scraping',
          language === 'pt' ? 'Integração com múltiplas APIs' : 'Multiple APIs integration',
          language === 'pt' ? 'Dashboard de monitoramento' : 'Monitoring dashboard',
          language === 'pt' ? 'Relatórios de execução' : 'Execution reports',
          language === 'pt' ? 'Agendamento de tarefas' : 'Task scheduling',
          language === 'pt' ? 'Tratamento de erros' : 'Error handling'
        ]
      }
    }
  ];

  const filteredProjects = activeFilter === 'all' 
    ? projects 
    : projects.filter(project => project.category === activeFilter);

  // Pagination logic
  const totalPages = Math.ceil(filteredProjects.length / projectsPerPage);
  const startIndex = (currentPage - 1) * projectsPerPage;
  const currentProjects = filteredProjects.slice(startIndex, startIndex + projectsPerPage);

  const nextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  const prevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const openModal = (project: Project) => {
    setSelectedProject(project);
  };

  const closeModal = () => {
    setSelectedProject(null);
  };

  // Função para lidar com clicks em links
  const handleDemoClick = (project: Project) => {
    return handleLinkClick(project.demo, 'demo');
  };

  const handleGithubClick = (project: Project) => {
    return handleLinkClick(project.github, 'github');
  };

  return {
    filters,
    projects: currentProjects,
    activeFilter,
    setActiveFilter,
    currentPage,
    totalPages,
    nextPage,
    prevPage,
    selectedProject,
    openModal,
    closeModal,
    handleDemoClick,
    handleGithubClick,
    hasProjects: filteredProjects.length > 0
  };
}